import { AuthGuard } from './shared/guards/auth.guard';
import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { RouterConstants } from './shared/config/constants/router.constants';

const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./pages/login/login.module').then( m => m.LoginPageModule),
  },
  {
    path: 'login',
    loadChildren: () => import('./pages/login/login.module').then( m => m.LoginPageModule),
  },
  {
    path: 'tabs',
    loadChildren: () => import('./pages/tabs/tabs.module').then(m => m.TabsPageModule),
    canActivate:[AuthGuard]
  },
  {
    path: 'achievement-list/detail/:id',
    loadChildren: () => import('./pages/achievement-list/achievement-detail/achievement-detail.module').then( m => m.AchievementDetailPageModule)
  },
  {
    path: 'prize-list/detail/:id',
    loadChildren: () => import('./pages/prize-list/prize-detail/prize-detail.module').then( m => m.PrizeDetailPageModule)
  },
  {path: '**', redirectTo: RouterConstants.LOGIN},
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
