import { TranslateService } from '@ngx-translate/core';
import { GamesEnum } from './../../models/game_play.model';
import { Player } from '../../models/game_play.model';
import { GlobalDataHelperService } from './global-data-helper.service';
import { StorageService } from './../storage/storage.service';
import { Injectable } from '@angular/core';
import { Device } from '@capacitor/device';
import { UserSession } from 'src/app/shared/models/user.model';
import { StorageConstants } from 'src/app/shared/config/constants/storage.constants';
import { App } from '@capacitor/app';
import { GamePlay } from '../../models/game_play.model';
import { Dialog } from '@capacitor/dialog';
import { Plugins } from '@capacitor/core';
const { NativeMarket } = Plugins;
// import { Browser } from '@capacitor/browser';

@Injectable({
  providedIn: 'root'
})
export class GlobalDataService {

  public userSession: UserSession;
  public gamePlay: GamePlay;
  public player: Player;
  platform: string;

  redirectionURL: string;
  constructor(
    private storageService: StorageService,
    private gdHelper: GlobalDataHelperService,
    private translate: TranslateService,
  ) {
    this.initGlobalData(GamesEnum.Roulette);
    this.userSession = new UserSession();
  }

  initGlobalData(gameCode: GamesEnum) {
    this.gamePlay = new GamePlay();
    this.player = new Player();
    this.gamePlay.gameCode = gameCode;
  }

  async generatePlay(gameCode: GamesEnum, gameState?: any): Promise<any> {
    return new Promise(async (resolve, reject) => {
      try {
        let data = await this.gdHelper.playGame(gameCode, gameState).toPromise();
        console.log('Play from server', data);
        resolve(data);
      } catch (error) { reject(error); }
    });
  }

  async loadStoredSession() {
    return new Promise(async (resolve, reject) => {
      this.platform = (await Device.getInfo()).platform;
      this.userSession = await this.storageService.getDataSecure(StorageConstants.USER_DATA);
      let storedSession = this.userSession;
      if (!this.userSession) { this.userSession = new UserSession(); }
      this.userSession.uuid = (await Device.getId()).uuid;
      if (this.platform !== 'web') { this.userSession.appVersion = (await App.getInfo()).version; }
      console.log('Session', this.userSession)
      resolve(storedSession);
    });
  }

  canPlayGame() {
    if (this.gamePlay.canActivateGame && !this.gamePlay.playingGame && this.gamePlay.inOffice && !this.gamePlay.receivingReward) {
      return true;
    } else return false;
    // if (!this.gamePlay.canActivateGame || this.gamePlay.playingGame || !this.gamePlay.inOffice) {
    //   return true;
    // } else return false;
  }

  showTemporizer() {
    if (!this.gamePlay.maxPlays && this.gamePlay.nextSpin && !this.gamePlay.receivingReward && !this.gamePlay.playingGame) {
      return true;
    } else return false;
  }

  showOfficeView() {
    if (!this.gamePlay.inOffice && !this.gamePlay.nextSpin && !this.gamePlay.maxPlays) {
      return true;
    } else return false;
  }

  showMaxPlays() {
    if (this.gamePlay.maxPlays && !this.gamePlay.playingGame && !this.gamePlay.receivingReward) {
      return true;
    } else return false;
  }

  async saveSession() {
    return new Promise(async (resolve, reject) => {
      await this.storageService.storeDataSecure(StorageConstants.USER_DATA, this.userSession);
      resolve(true);
    });
  }

  async removeSession() {
    return new Promise(async (resolve, reject) => {
          // return this.storageService.clearSecure();
      await this.storageService.removeDataSecure(StorageConstants.USER_DATA);
      await this.storageService.removeDataSecure(StorageConstants.TOKEN);
      this.userSession = new UserSession();
      resolve(true);
    });
  }

  async handleUpdateVersion() {
    let result = await Dialog.confirm({
      title: this.translate.instant('PAGES.PROFILE.CONFIGURATION.UPDATE_TITLE'),
      message: this.translate.instant('PAGES.PROFILE.CONFIGURATION.UPDATE_DESCRIPTION'),
    });
    if (result && result.value) {
      this.openMarket();
    }
  }

  openMarket() {
    console.log('Opening market...')
    if (this.platform === 'android') {
      // await Browser.open({ url: 'market://developer?id=com.tsystems.magentatribe' });
      NativeMarket.openStoreListing({ appId: 'com.tsystems.magentatribe' });
    } else {
      
    }
  }

}
