import { Injectable } from '@angular/core';
import { AlertController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class AlertService {

  constructor(
    private alertController: AlertController,
  ) { }

  async showBasicAlert(header, message, buttons, inputs?) {
    return new Promise(async (resolve, reject) => {
      const alert = await this.alertController.create({
        cssClass: 'custom-basic-alert',
        header, message, buttons, inputs,
      });
      await alert.present();
      const data = await alert.onDidDismiss();
      resolve(data);
    });
  }

  async showGameAlert(header, message, buttons) {
    return new Promise(async (resolve, reject) => {
      const alert = await this.alertController.create({
        cssClass: 'custom-game-alert',
        animated: true, header, message, buttons
      });
      await alert.present();
      const data = await alert.onDidDismiss();
      resolve(data);
    });
  }

}
