import { TranslateService } from '@ngx-translate/core';
import { Geolocation, Position } from '@capacitor/geolocation';
import { AlertService } from './../alert/alert.service';
import { IBeacon } from '@ionic-native/ibeacon/ngx';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ConnectivityService {

  private officeState = new Subject<any>();
  constructor(
    private ibeacon: IBeacon,
    private alertService: AlertService,
    private translate: TranslateService,
  ) { }
  
  getOfficeState(): Subject<any> {
    return this.officeState;
  }

  async geolocationRequestPermission() {
    return Geolocation.requestPermissions();
  }

  async geolocationGetPosition() {
    return new Promise<Position>(async (resolve, reject) => {
      resolve(await Geolocation.getCurrentPosition());
    });
  }

  async checkBluetoothAvailability() {
    return this.ibeacon.isBluetoothEnabled();
  }

  async checkLocationAvailability() {
    return new Promise(async (resolve, reject) => {
      try {
        let location = false;
        let permission = await Geolocation.checkPermissions();
        console.log('Permission', permission);
        if (permission && permission.location === 'granted') {
          let gps = await Geolocation.getCurrentPosition();
          location = true;
        }
        resolve(location);
      }catch (err) {
        console.log('ERR location', err);
        resolve(false);
      }
    });
  }

  async showAlertConnectivity() {
    let bluetooth = await this.checkBluetoothAvailability();
    let location = await this.checkLocationAvailability();
    console.log('Connectivity', bluetooth, location);
    if (!bluetooth || !location) {
      await this.alertService.showGameAlert(this.translate.instant('PAGES.GAME.BLUETOOTH'),this.translate.instant('PAGES.GAME.BLUETOOTH_DESCRIPTION'),[this.translate.instant('ALERTS.ACCEPT')]);
    }
  }

  async beaconSearch(beaconUUID) {
    console.log('Beacon search ', beaconUUID);
    const delegate = this.ibeacon.Delegate();

    delegate.didEnterRegion().subscribe(async data => {
      console.log('didEnterRegion: ', data);
      this.officeState.next(true);
      // this.insideOffice();
    }, error => console.error('ERROR DID ENTER REGION', error));

    delegate.didExitRegion().subscribe(async data => {
      console.log('didExitRegion: ', data);
      this.officeState.next(false);
      // this.notInOffice();
    }, error => console.error('ERROR DID ENTER REGION', error));

    delegate.didDetermineStateForRegion().subscribe(async data => {
      console.log('didDetermineStateForRegion: ', data);
      if (data.state === "CLRegionStateInside") { this.officeState.next(true); }
      else if (data.state === "CLRegionStateOutside") { this.officeState.next(false); }
      // if (data.state === "CLRegionStateInside") { this.insideOffice(); }
      // else if (data.state === "CLRegionStateOutside") { this.notInOffice(); }
    }, error => console.error('ERROR DETERMINE STATE FOR REGION', error));

    delegate.didRangeBeaconsInRegion().subscribe(async data => {
      console.log('didRangeBeaconsInRegion: ', data);
    }, error => console.error('ERROR RANGE BEACONS IN REGION', error));

    const beaconRegion = this.ibeacon.BeaconRegion('deskBeacon', beaconUUID);

    this.ibeacon.startMonitoringForRegion(beaconRegion).then(() => {
      console.log('Native layer received the request to monitoring');
    }, error => console.error('Native layer failed to begin monitoring: ', error));
  }

}
