import { GlobalDataService } from 'src/app/shared/services/globalData/global-data.service';
import { LanguageService } from './shared/services/language/language.service';
import { ConnectivityService } from './shared/services/connectivity/connectivity.service';
import { IBeacon } from '@ionic-native/ibeacon/ngx';
import { Component, NgZone } from '@angular/core';
import { StatusBar, Style } from '@capacitor/status-bar';
import { ScreenOrientation } from '@ionic-native/screen-orientation/ngx';
import { Platform } from '@ionic/angular';
import { Geolocation } from '@capacitor/geolocation';
import { PushNotificationsService } from './shared/services/push-notifications/push-notifications.service';
import { App, URLOpenListenerEvent } from '@capacitor/app';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {
  constructor(
    private screenOrientation: ScreenOrientation,
    private ibeacon: IBeacon,
    private zone: NgZone,
    private platform: Platform,
    private connectivity: ConnectivityService,
    private language: LanguageService,
    private pushNotificationService: PushNotificationsService,
    private globalDataService: GlobalDataService,
  ) {
    this.initializeApp();
    this.requestPermissions();
  }

  async requestPermissions() {
    this.platform.ready().then(async () => {
      let that = this;
      setTimeout(async () => {
        await that.ibeacon.requestAlwaysAuthorization();
        await this.connectivity.geolocationRequestPermission();
      }, 500);
    });
  }

  async initializeApp() {
    this.platform.ready().then(async () => {
      await this.language.initLanguage();
      this.universalLinkListener();
      this.pushNotificationService.register();
      this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);
      await StatusBar.setBackgroundColor({ color: '#E20074' });
      // await SplashScreen.hide();
      // setTimeout(async () => { { await SplashScreen.hide(); } }, 500)
    });
  }

  universalLinkListener() {
    App.addListener('appUrlOpen', (event: URLOpenListenerEvent) => {
      this.zone.run(() => {
        // event.url = "https://gotoffice.t-systems.es/";
        const appPath = event.url.split(environment.UNIVERSAL_DOMAIN).pop();
        console.log('URL received', event.url, 'APP domain', environment.UNIVERSAL_DOMAIN, appPath);
        if(appPath) this.globalDataService.redirectionURL = appPath;
      })
    })
  }
}
