import { StorageService } from './../storage/storage.service';
import { Injectable } from '@angular/core';
import { StorageConstants } from '../../config/constants/storage.constants';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(
    private storageService: StorageService
   ) { }

  async clearUserToken() {
    await this.storageService.clearSecure();
    // await this.storageService.removeDataSecure(StorageConstants.TOKEN);
    // await this.storageService.removeDataSecure(StorageConstants.USER_DATA);
  }

  async setToken(token: string) {
    await this.storageService.storeDataSecure(StorageConstants.TOKEN, token);
  }

  async getToken() {
    return await this.storageService.getDataSecure(StorageConstants.TOKEN);
  }

}
