import { TranslateService } from '@ngx-translate/core';
import { Router } from '@angular/router';
import { GlobalDataService } from 'src/app/shared/services/globalData/global-data.service';
import { Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { RouterConstants } from '../config/constants/router.constants';

@Injectable({
  providedIn: 'root'
})
export class ErrorHandlerService {

  constructor(
    private gb: GlobalDataService,
    private router: Router,
    public translate: TranslateService,
  ) { }

  async showMessage(error: HttpErrorResponse): Promise<string> {
    return new Promise(async (resolve, reject) => {
      let text = this.translate.instant('ERRORS.GENERIC');
      switch (error.status) {
        case 0:
          text = this.translate.instant('ERRORS.0');
          break;
        case 310:
          text = this.translate.instant('ERRORS.310');
          window.location.href = error.error.url;
          break;
        case 400:
          if (error.error && error.error.code) {
            text = this.translate.instant('ERRORS.400.'+error.error.code);
          } else {
            text = this.translate.instant('ERRORS.GENERIC');
          }
          break;
        case 401:
          text = this.translate.instant('ERRORS.401');
          this.gb.userSession.tokenJWT = undefined;
          console.log('Route of 401', this.router.url, RouterConstants.LOGIN, this.router.url !== RouterConstants.LOGIN)
          if (this.router.url !== RouterConstants.LOGIN && this.router.url !== '/') {
            this.router.navigateByUrl(RouterConstants.LOGIN, { replaceUrl: true });
          } else { await this.gb.removeSession(); }
          break;
        case 403:
          text = this.translate.instant('ERRORS.403');
          break;
        case 426:
          text = this.translate.instant('ERRORS.426')
          break;
        case 500:
          text = this.translate.instant('ERRORS.500');
          break;
        case 505:
          text = this.translate.instant('ERRORS.505');
          await this.gb.handleUpdateVersion();
          break;
        case 525:
          text = error.error.message;
          break;
        case 525:
          text = error.error.message;
          break;
        default:
          text = this.translate.instant('ERRORS.GENERIC');
      }
      resolve(text);
    });
  }

}
