import { GamesEnum } from './../../models/game_play.model';
import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';
import { ApiService } from 'src/app/shared/services/api/api.service';
import { Observable, throwError } from 'rxjs';
import { map, catchError,  } from 'rxjs/operators';
import { RequestEndpoints } from 'src/app/shared/config/constants/api.constants';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class GlobalDataHelperService {

  constructor(
    private apiService: ApiService,
  ) { }

  playGame(gameCode: GamesEnum, gameState?: any): Observable<any>{
    let httpParams = new HttpParams();
    httpParams = httpParams.append('game', gameCode);
    if (gameState) httpParams = httpParams.append('gameState', JSON.stringify(gameState));
    return this.apiService.post(environment.API_URL + RequestEndpoints.PLAY, httpParams).pipe(
      map(async (data: any) => {
        return data;
    }),catchError(error => throwError(error)));
  }

}

