import { GlobalDataService } from 'src/app/shared/services/globalData/global-data.service';
import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root'
})
export class LanguageService {

  currentLanguage: string;
  constructor(
    private translate: TranslateService,
    private globalData: GlobalDataService,
  ) { }

  async initLanguage() {
    return new Promise(async (resolve, reject) => {
      await this.globalData.loadStoredSession();
      if (this.globalData.userSession.language) {
        this.changeLanguage(this.globalData.userSession.language);
      } else {
        this.changeLanguage('es');
      }
      resolve(true);
    });
  }

  changeLanguage(language: string) {
    console.log('Language used: ' + language);
    this.translate.use(language);
    this.translate.setDefaultLang(language);
    this.currentLanguage = language;
  }

  getCurrentLanguage(): string {
    return this.currentLanguage;
  }
}
